import api from '../../common/request.js'	
	
export default {
	data() {
		return {
			scrollHeight: 0,
			titlelist: [],
			currentIndex: 0,
			booklist: [],
			channel: 'man'
		}
	},
	onLoad() {
		
	},
	onReady() {
		const windowHeight = wx.getSystemInfoSync().windowHeight
		let sHeight = windowHeight - 43
		this.scrollHeight = sHeight * 2
		this.getTitleData()
	},
	methods: {
		selectTab(e) {
		  this.channel = e.currentTarget.dataset.channel
			console.log(this.channel)
		  this.currentIndex = 0
		  this.getTitleData()
		},
		selectTitle(e) {
		  let index = e.currentTarget.dataset.index
		  let id = e.currentTarget.dataset.id
		  this.currentIndex = index
		  this.getBookData(id)
		},
		getTitleData() {
			api.get('/rank/miniRank', {
				channel: this.channel
			}).then(res => {
				if (res.code === 200) {
					let titleId = res.data[0].id
					this.titlelist = res.data
					this.getBookData(titleId)
				}
			})
		},
		 getBookData(id) {
		  api.post('/rank/rankDetail', {
		    rank_id: id
		  }).then(res => {
		    if (res.code === 200) {
					this.booklist = res.data
		    }
		  })
		}
		
	}
	
}	
